



































































































import is from 'is'
import Utils from '@/utils/index'
import api from '@/utils/api'
import mixin from '@/utils/mixin'
export default {
	mixins:[mixin],
	computed: {
	    userInfo() {
	        return this.$store.state.app.userInfo;
	    }
	},
	data() {
		return {
			starnum:3,
			is_show:false,
			current_sign:0,
			current_info:{},
			current_imgs:[],
			bid:0,
			signshow:false,
			signerror:false
		};
	},
	methods:{
		async init(){
			let bid = uni.getStorageSync("bid")
			this.bid = bid
			Utils.loading('正在加载')
			let missionDetail = await api.getMission({
				uid:this.userInfo.uid,
				bid:bid
			})
			if(missionDetail.status == 1){
				Utils.loaded()
				let data = missionDetail.data
				this.current_sign = data.current_sign
				this.current_info = data.current_info
				this.current_imgs = this.current_info.detail_photo ? JSON.parse(this.current_info.detail_photo) : []
			}
		},
		showModel(){
			let isshow = this.is_show
			if(isshow){
				this.is_show = false
			}else{
				this.is_show = true
			}
		},
		showSignModel(){
			let isshow = this.signshow
			if(isshow){
				this.signshow = false
			}else{
				this.signshow = true
			}
		},
		showerrorModel(){
			let isshow = this.signerror
			if(isshow){
				this.signerror = false
			}else{
				this.signerror = true
			}
		},
		// 签到
		async actionSign(){
			let actionResult = await api.actionSign({
				uid:this.userInfo.uid,
				bid:this.bid
			})
			if(actionResult.status == 1){
				this.current_sign = actionResult.data
				this.signshow = true
			}else{
				this.signerror = true;
			}
		},
		// 提交任务
		async finishMission(){
			
		}
		
	},
	onShow(){
		let _this = this
		if(!_this.shouldLogin){
			_this.init()
		}
	}
}
